.date {
    position: relative;
}

.date input[type="date"] {
    /*url(https://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/calendar_2.png)*/
    background: rgba(0, 0, 0, 0.5) 97% 50% no-repeat;
    height: 50px;
    width: 100%;
    border-radius: 10px;
    outline: none;
    border: none;
    padding: 0 10px;
    color: #fff;
    font-size: 19px;
    margin: 2px 0 15px 0;
}

.date input[type="date"]::-webkit-calendar-picker-indicator {
    filter: invert(1);
    cursor: pointer;
}

.date input::-webkit-datetime-edit, input::-webkit-inner-spin-button, input::-webkit-clear-button {
    opacity: 0;
}

.date span {
    position: absolute;
    top: 14px;
    left: 15px;
    z-index: 10;
    font-size: 18px;
}