.mobile_field {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
    padding-bottom: 20px;

}

.mobile_field span {
    position: absolute;
    right: 0;
    bottom: 10px;
    cursor: pointer;
    color: #efefef;
}

.radio_btn {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.form {
    height: 0;
    transition: .5s all ease-out;
    overflow: hidden;
}

.text_area{
    width: 100%;
    height: 90px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    outline: none;
    border: none;
    padding: 10px 10px;
    color: #fff;
    font-size: 19px;
    margin: 15px 0 15px 0;
}

.text_area::-webkit-input-placeholder {
    font-family: Roboto, 'sans-serif';
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    color: #949494;
}